.task__title {
  text-align: center;
  color: #fff;
  background-color: #000;
  justify-content: center;
  padding: 15px;
  font-size: 28px;
  display: flex;
}

.task__list {
  background-color: #a8a8a8;
  flex-flow: wrap;
  justify-content: center;
  gap: 20px;
  padding: 30px;
  display: flex;
}

.task__item {
  text-align: center;
  width: 300px;
  background-color: #ff6b0a;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  list-style: none;
  transition: background-color .25s;
}

.task__item:hover, .task__item:focus {
  background-color: #000;
}

.task__link {
  text-align: center;
  color: #212121;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: Helvetica Neue, sans-serif;
  font-size: 24px;
  text-decoration: none;
  transition: color .25s;
  display: block;
}

.task__link:hover, .task__link:focus {
  color: #fff;
}

